.container {
  display: flex;
  height: 100vh;
  width: 100%;
}

.content_wraper {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  /* background-color: #e0e0e0; */
  background-color: #F0F0F0;
}

.content {
  margin-top: 20px;
}

.hidden {
  display: none;
}

.fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
